@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
*,*::before,*::after{box-sizing:border-box;padding:0;margin:0}body{margin:0;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}button{border:0;font-family:Roboto}
.burger__menu--header{width:21px;height:16px}.burger__menu--header span{border-radius:1px;background-color:#121212}@media(min-width: 768px){.burger__menu--header{display:none}}.burger__menu--aside{width:33px;height:18px;display:none}.burger__menu--aside span{background-color:#fff}@media(min-width: 768px){.burger__menu--aside{display:block}}@media(min-width: 1024px){.burger__menu--aside{width:24px}}.burger__menu{position:relative;cursor:pointer}.burger__menu span{position:absolute;width:100%;height:3px;left:0;border-radius:2px}.burger__menu span:nth-child(2){top:45%}.burger__menu span:nth-child(3){top:90%}
.location{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}.location__place{color:#999;font-size:14px}
.header__container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding-left:16px;padding-right:16px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding-top:10px}@media(min-width: 768px){.header__container{-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding-right:32px;padding-left:32px}}.header__block{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.header__title{font-size:30px;color:#0ec261;font-weight:700}
.content{margin-bottom:100px}.content__container{padding-left:16px;padding-right:16px}@media(min-width: 768px){.content__container{padding-right:32px;padding-left:32px}}.content__title{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:16px}.content__title span{font-size:32px}@media(min-width: 768px){.content__title span{font-size:70px}}.content__name{color:#0ec261}.content__text{font-size:18px;color:#999;margin-bottom:32px}@media(min-width: 768px){.content__text{font-size:26px}}.content__button{width:100%;height:50px;background:linear-gradient(to right, #0ec261, #039f67);color:#fff;font-size:18px}@media(min-width: 768px){.content__button{width:250px;height:48px;margin-left:32px;margin-right:32px;border-radius:10px}}@media(min-width: 768px){.content__subtitle{font-size:70px}}@media(min-width: 768px){.content__name{font-size:70px}}
.footer{width:100%;height:72px;background-color:#151b1f}@media(min-width: 768px){.footer{background-color:transparent}}.footer__container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-end;align-items:flex-end;-webkit-justify-content:space-around;justify-content:space-around;height:inherit;padding:16px}@media(min-width: 768px){.footer__container{background-color:transparent;padding-left:32px;padding-right:32px;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}}.footer__tel{font-size:13px;margin-bottom:8px;color:#0ec261}@media(min-width: 768px){.footer__tel{-webkit-order:1;order:1;color:#121212;text-decoration:none}}.footer__copyright{font-size:13px;color:#eee}@media(min-width: 768px){.footer__copyright{color:#999}}
.main{grid-area:m;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between}
.language{width:47px;height:47px;background-color:transparent;border-radius:50%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;transition:.2s all}.language:hover{border:1px solid #eee;transition:.2s all}.language:hover .language__button{background-color:transparent;color:#0ec261}.language__button{background-color:transparent;color:#fff;transition:.2s all}.language__button:hover{color:#0ec261;transition:.2s all}
.aside{display:none;grid-area:a;background-color:#151b1f}@media(min-width: 768px){.aside{display:block}}.aside__container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:100%;padding-right:27px;padding-left:27px;padding-top:35px;padding-bottom:27px}
.slider{grid-area:s;background-color:#000}
.page{overflow:hidden;display:grid;grid-template-columns:1fr;grid-template-rows:1fr;grid-template-areas:"m";min-height:100vh}@media(min-width: 768px){.page{grid-template-columns:85px 1fr;grid-template-rows:1fr;grid-template-areas:"a m"}}@media(min-width: 1024px){.page{grid-template-columns:65px 1fr 410px;grid-template-rows:1fr;grid-template-areas:"a m s"}}
